<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>

  <div class="router-back" @click="routerBack()">
    <i class="fal fa-arrow-square-left"></i> 
  </div>

  <el-form ref="project" class="project-form project-form-view" label-width="90px" style="width:900px;">
    <div class="header">
      <h1 class="title">
        <!-- <span class="desc">:</span>-->
        {{projectInfo.title}}
      </h1>
      <span>{{projectInfo.clientName}}</span>
      <div class="ctrl-bar">
        <el-button-group>
          <el-button type="default" icon="fal fa-edit"> </el-button>
          <el-button type="danger" icon="el-icon-delete"></el-button>
        </el-button-group>
      </div>
    </div>

    <el-collapse v-model="activeNames" @change="collapseHandleChange">
      <el-collapse-item name="1" title="">
        <div class="form-wrapper">
        <el-row>
          <el-col :span="9">
            <el-form-item label="MIS">
              {{projectInfo.MIS}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              {{projectInfo.succBidder}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              {{projectInfo.itemAmount}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="9">
            <el-form-item label="">
              {{projectInfo.PST}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              {{projectInfo.seller}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              {{projectInfo.salesman}}
            </el-form-item>
          </el-col>

        </el-row>

        <el-row>

          <el-col :span="9">
            <el-form-item label="">
              {{projectInfo.PET}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              {{projectInfo.stage}}
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              {{projectInfo.type}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="">
              {{projectInfo.desc}}
            </el-form-item>
          </el-col>
        </el-row>
        </div>
      </el-collapse-item>
      <el-collapse-item title="" name="2">
        <div class="ctrl-bar">
          <el-button-group>
            <el-button type="default" icon="fal fa-plus"> </el-button>
            <el-button type="default" icon="fal fa-square"> </el-button>
          </el-button-group>
        </div>
        <el-table :data="projectInfo.taskList" style="width: 100%">
          <el-table-column prop="title" label="" width="200"></el-table-column>
          <el-table-column prop="publisher" label="" width="100"></el-table-column>
          <el-table-column prop="startDate" label="" width="150"></el-table-column>
          <el-table-column prop="endDate" label="" width="150"></el-table-column>
          <el-table-column prop="delay" label="" width="100"></el-table-column>
          <el-table-column prop="type" label="" width="80"></el-table-column>
          <el-table-column prop="levle" label="" width="80"></el-table-column>
          <el-table-column prop="rate" label="" width="80"></el-table-column>
          <el-table-column prop="details" label="" width="580"></el-table-column>
          <el-table-column prop="comments" label="" width="200"></el-table-column>
        </el-table>
      </el-collapse-item>
      <el-collapse-item title="" name="3">
        <div class="ctrl-bar">
          <el-button-group>
            <el-button type="default" icon="fal fa-plus"> </el-button>
            <el-button type="default" icon="fal fa-square"> </el-button>
          </el-button-group>
        </div>
        <el-table :data="projectInfo.docList" style="width: 100%">
          <el-table-column prop="state" label="" width="150"></el-table-column>
          <el-table-column prop="title" label="" width="200"></el-table-column>
          <el-table-column prop="task" label=""></el-table-column>
        </el-table>
      </el-collapse-item>
    </el-collapse>

  </el-form>

</div>
</template>

<script>
export default {
  data() {
    return {
      projectInfo: "",
      activeNames: ['1']
    };
  },
  watch: {},
  methods: {
    test() {
      console.log("test");
      return;
    },
    routerBack() {
      this.$router.back(-1);
    },
    collapseHandleChange(val) {
      console.log(val);
    },
    getData() {
      var url = this.$urls.project;
      this.$http.get(url).then(
        response => {
          var data = JSON.parse(response.bodyText);
          this.projectInfo = data.project.list[parseInt(this.$route.params.id) - 1]
        },
        error => {}
      );
    }
  },
  mounted() {
    this.getData();
  }
};
</script>

<style lang="less">
</style>



