<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>
  
  <el-form ref="project" class="project-form project-form-edit" :model="project" label-width="90px" style="width:825px;">
    <el-row>
      <el-col :span="24">
        <el-form-item label="">
          <el-input v-model="project.title"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    
    <el-row>
      <el-col :span="17">
        <el-form-item label="">
          <el-input v-model="project.clientName"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="">
          <el-input v-model="project.itemAmount"></el-input>
        </el-form-item>
      </el-col>
      
    </el-row>
    <el-row>
      <el-col :span="10">
        <el-form-item label="MIS">
          <el-input v-model="project.MIS"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="">
          <el-input v-model="project.seller"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="">
          <el-input v-model="project.salesman"></el-input>
        </el-form-item>
      </el-col>
    </el-row>

    
    <el-row>
      <el-col :span="10">
        <el-form-item label="">
          <el-date-picker style="width:100%;" v-model="project.SEdate" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="" end-placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="14">
        <el-form-item label="">
          <el-radio-group v-model="project.succBidder">
            <el-radio-button v-for="item in project.succBidderList" :label="item" :key="item">{{item}}</el-radio-button>
          </el-radio-group>
          
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="">
          <el-checkbox-group v-model="project.type">
            <el-checkbox-button v-for="item in project.typeList" :label="item" :key="item">{{item}}</el-checkbox-button>
          </el-checkbox-group>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="">
          <el-radio-group v-model="project.stage">
            <el-radio-button v-for="item in project.stageList" :label="item" :key="item">{{item}}</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="">
          <el-input type="textarea" v-model="project.desc" rows="5"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    
    <el-form-item>
      <el-button type="primary" @click="onSubmit"></el-button>
      <el-button></el-button>
    </el-form-item>
  </el-form>


</div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      project: {
        title: "",
        type:[],
        stage:"",
        succBidder:"",
        MIS: "",
        itemAmount: "",
        clientName: "",
        seller: "",
        salesman: "",
        SEdate:"",
        desc: "",
        succBidderList:["","",""],
        typeList:["","","","","","","","",""],
        stageList:["","","","","",""]
      }
    };
  },
  watch: {},
  methods: {
    onSubmit() {
      console.log("submit!");
      
    }
  },
  mounted() {}
};
</script>

<style lang="less">
</style>



