import Vue from 'vue'
import App from './App'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import VueResource from "vue-resource"
Vue.use(VueResource);

import router from './router.js'
import 'assets/less/main.less';
Vue.config.productionTip = false

import vuex from "./vuex.js";
Vue.prototype.$vuex = vuex;
import {storage,session,cookie,timer,handleClass,BPInfo} from "./model/common.js";//js
Vue.prototype.$timer = timer;//vuethis.$timer
Vue.prototype.$storage = storage;//storage,cookie
Vue.prototype.$session = session;//session
Vue.prototype.$cookie = cookie;//cookie
Vue.prototype.$handleClass = handleClass;//domclass,bodyhtmlclass
Vue.prototype.$BPInfo = BPInfo;//domclass,bodyhtmlclass
//import {datap} from "./datap.js";
//Vue.prototype.$datap = datap;
import {urls} from "./urls.js";
Vue.prototype.$urls = urls;

//import Icon from 'vue-svg-icon/Icon.vue';
import Icon from './model/Icon.vue';//vue-svg-icon
Vue.component('icon', Icon); 

//fontawesomePro,webfont
import 'fa/less/fontawesome.less';
import 'fa/less/light.less';
import 'fa/less/solid.less';
//import 'fa/less/regular.less';
//import 'fa/less/brands.less';
