<template>
<div class="content-wrapper">
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.sub}}</el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>

  <div class="ctrl-bar">
    <el-button-group v-if="toolState=='1'">
      <el-button type="default" icon="fal fa-check-square"> </el-button>
      <el-button type="default" icon="el-icon-share"> </el-button>
      <el-button type="default" icon="fal fa-chart-bar"> </el-button>
      <el-button type="danger" icon="el-icon-delete"></el-button>
    </el-button-group>
    <div class="input-wrapper" v-if="toolState=='2'">
      <el-select v-model="selectType" slot="prepend" placeholder="" class="w100">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="2"></el-option>
        <el-option label="" value="3"></el-option>
        <el-option label="" value="4"></el-option>
        <el-option label="" value="5"></el-option>
      </el-select>
      <el-autocomplete class="input-client w200" prefix-icon="fal fa-user" v-model="inputClient" :fetch-suggestions="querySearchClient" placeholder="" @select="handleSelect"></el-autocomplete>
      <el-date-picker v-model="inputSEDate" type="daterange" align="right" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions"></el-date-picker>
      <el-autocomplete class="input-client w200" prefix-icon="el-icon-search" v-model="inputTitle" :fetch-suggestions="querySearchTitle" placeholder="" @select="handleSelect"></el-autocomplete>
      <el-button type="primary" icon="el-icon-search"></el-button>
    </div>
    <el-radio-group class="tool-toggle" v-model="toolState">
      <el-radio-button label="1"><i class="fal fa-square"></i> </el-radio-button>
      <el-radio-button label="2"><i class="fal fa-search"></i> </el-radio-button>
    </el-radio-group>
  </div>

  <el-table :data="project" style="width: 100%" max-height="500" stripe @selection-change="handleSelectionChange" class="table-list">
    <el-table-column type="selection" v-if="toolState=='1'" width="50">
    </el-table-column>
    <el-table-column prop="id" label="" width="50" fixed>
    </el-table-column>
    <el-table-column prop="title" label="" fixed width="300">
    </el-table-column>
    <el-table-column prop="type" label="" width="100" sortable>
    </el-table-column>
    <el-table-column prop="stage" label="" width="100" sortable>
    </el-table-column>
    <el-table-column prop="succBidder" label="" width="100">
    </el-table-column>
    <el-table-column prop="MIS" label="MIS" width="250">
    </el-table-column>
    <el-table-column prop="itemAmount" label="" width="100">
    </el-table-column>
    <el-table-column prop="clientName" label="" width="300">
    </el-table-column>
    <el-table-column prop="seller" label="" width="100">
    </el-table-column>
    <el-table-column prop="salesman" label="" width="100">
    </el-table-column>
    <el-table-column prop="PST" label="" width="200" sortable>
    </el-table-column>
    <el-table-column prop="PET" label="" width="200" sortable>
    </el-table-column>
    <el-table-column prop="desc" label="" width="200">
    </el-table-column>
    <el-table-column fixed="right" label="" width="180">
      <template slot-scope="scope">
        <el-button @click="tableRowBtnView(scope.row)" type="text" size="small"></el-button>
        <el-button @click="dialogPopup(scope.row,'NewTask')" type="text" size="small"></el-button>
        <el-button @click="dialogPopup(scope.row,'NewDoc')" type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400"></el-pagination>
  <v-dialog :dialog.sync="dialog" @dialogClose="dialogClose"></v-dialog>
</div>
</template>

<script>
import Dialog from "cps/features/project/dialog/index-dialog.vue"
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      toolState: "2",
      project: [],
      inputTitle: "",
      inputClient: "",
      inputSEDate: "",
      selectType: "",
      selectClient: "",
      multipleSelection: [],
      projectClientList: [],
      projectTitleList: [],
      currentPage: 1,
      dialog:{
        show:false,
        form:"",
        content:""
      }

    };
  },
  components:{
    "v-dialog":Dialog
  },
  watch: {
    toolState: function() {
      console.log(this.toolState);
    }
  },
  methods: {
    dialogPopup(content,form){
      this.dialog.show=true;
      this.dialog.form=form;
      this.dialog.content=content;
    },
    dialogClose(val){
      this.dialog.show=val;
    },
    handleSizeChange(val) { //
      console.log(` ${val} `);
    },
    handleCurrentChange(val) { //
      console.log(`: ${val}`);
    },
    querySearchClient(queryString, cb) { //-
      //console.log(this.client);
      var list = this.projectClientList;
      var results = queryString ? list.filter(this.createFilter(queryString)) : list;
      //  callback 
      cb(results);
    },
    querySearchTitle(queryString, cb) { //-
      //console.log(this.client);
      var list = this.projectTitleList;
      var results = queryString ? list.filter(this.createFilter(queryString)) : list;
      //  callback 
      cb(results);
    },
    createFilter(queryString) { //-
      return (list) => {
        return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) { //-
      console.log(item);
    },
    handleSelectionChange(val) { //
      this.multipleSelection = val;
    },
    tableRowBtnView(row) { // 
      this.$router.push({
        path: '/project/formView/' + row.id
      })
    },
    getData() {
      var url = this.$urls.project;
      this.$http.get(url).then(
        response => {
          var data = JSON.parse(response.bodyText);
          //console.log(data.project.list);
          this.project = data.project.list;
          var _project = data.project.list;
          for (var i = 0; i < _project.length; i++) {
            this.projectClientList.push({
              "value": _project[i].clientName
            })
            this.projectTitleList.push({
              "value": _project[i].title
            })
          }
        },
        error => {
          //console.log(err);
        }
      );
    }
  },
  mounted() {
    this.getData();
  }
};
</script>

<style lang="less">
</style>



